<template>
  <view class="content">
    <view class="u-search-inner">
      <!-- <u-sticky :enable="enable"> -->
      <u-search offset-top="20" style="width: 100%;" v-model="value" @custom="search" @search="search" placeholder="请输入要查找的名字" :animation="true">
      </u-search>
      <!-- </u-sticky> -->
    </view>

    <view class="pets-box">
      <view class="pet-box" @click="showPet(petInfo.id)" v-for="petInfo in petInfos">
        <u-row gutter="">
          <u-col span="5">
            <view class="pet-image">
              <u-image width="250" height="250" border-radius="14rpx" :src="petInfo.images.split('#####')[0]"></u-image>
            </view>
          </u-col>
          <u-col span="6" class="pet-info">
            <u-row>
              <text class="pet-name">{{petInfo.pastName}}</text>
            </u-row>
            <u-row>
              <text class="pet-plots">{{petInfo.item}} > {{petInfo.branch}} > {{petInfo.dependant}}</text>
            </u-row>
            <u-row style="margin-top: 1em;">
              <text class="pet-position">分布地：</text>
              <text class="pet-position-v">{{petInfo.distributionArea}}</text>
            </u-row>
            <u-row justify="end" style="margin-top: 1em;">
              <text class="petInfo">查看具体信息> </text>
            </u-row>
          </u-col>
        </u-row>
      </view>
    </view>
    
    <!-- 消息提示框 -->
    <view>
      <u-toast ref="uToast" />
    </view>
    
    <!-- 加载更多组件 -->
    <view class="load-more">
      <u-loadmore :status="status" @loadmore="loadmore" :icon-type="iconType" :load-text="loadText" />
    </view>

    <!-- 回到最顶端 -->
    <u-tabbar v-model="current" :bg-color="bgColor" :border-top="borderTop" :list="tabbar" :mid-button="midButton"
      :inactive-color="inactiveColor" :activeColor="activeColor"></u-tabbar>
    <view class="wrap">
      <u-back-top :scroll-top="scrollTop"></u-back-top>
    </view>
    
  </view>
</template>

<script>
  export default {
    data() {
      return {
        value: '',
        current: 0,
        show: true,
        bgColor: '#ffffff',
        borderTop: true,
        tabbar: '',
        midButton: true,
        inactiveColor: '#909399',
        activeColor: '#5098FF',
        show: false,
        petInfos: [],
        hasNextPage: true,
        enable: true,
        scrollTop: 0,
        // 加载更多
        status: 'loadmore',
        iconType: 'circle',
        loadText: {
          loadmore: '轻轻上拉或点击',
          loading: '努力加载中',
          nomore: '实在没有了'
        },
        isPot: false,
        page: 1
      }
    },
    onPageScroll(e) {
      this.scrollTop = e.scrollTop;
    },
    onShow() {
      this.enable = true
    },
    onHide() {
      this.enable = false
    },
    onLoad() {
      this.tabbar = [{
          "pagePath": "/pages/folkMasters/index",
          "iconPath": "/static/icon/cum.png",
          "selectedIconPath": "/static/icon/cum-full.png",
          "text": "高手社区",
          customIcon: false,
        },
        {
          "pagePath": "/pages/expert/index",
          "iconPath": "/static/icon/rc.png",
          "selectedIconPath": "/static/icon/rc-full.png",
          "text": "专家社区",
          customIcon: false,
        },
        {
          "pagePath": "/pages/index/index",
          "iconPath": "/static/icon/scan.png",
          "selectedIconPath": "/static/icon/scan-full.png",
          "text": "扫一扫",
          "midButton": true,
          "customIcon": false,
        },
        {
          "pagePath": "/pages/petsLibrary/index",
          "iconPath": "/static/icon/bug.png",
          "selectedIconPath": "/static/icon/bug-full.png",
          "text": "智虫库",
          customIcon: false,
        },
        {
          "pagePath": "/pages/user/index",
          "iconPath": "account",
          "selectedIconPath": "account-fill",
          "text": "我",
          customIcon: false,
        }
      ];

      //请求害虫列表
      uni.request({
        url: 'http://ocr.server.truedei.com/ocr/pest/getPestList',
        method: 'POST',
        data: {
          "page": 1,
          "rows": 8,
        },
        success: res => {
          console.log(res);
          var data = res.data.result.list;
          this.petInfos = data;
        },
        fail: err => {
          reject(err);
        }
      });
    },
    watch: {
    	// 这里的演示为证明通过v-model绑定值，它是双向绑定的，意味着您无需监听change事件
    	// 也能知道value值当前的内容
    	value(val) {
    		// console.log(val);
    	}
    },
    methods: {
      showPet(petId) {
        uni.navigateTo({
          url:"/pages/petsLibrary/pet?id=" + petId,
          // url:"../component/classdetails/classdetails?newsid="+ newsid,
          // success:res =>{},
          // fail:() =>{},
          // complete:() => {}
        });
      },
      //加载更多方法
      loadmore(){
        
        //如果不是最后一页则继续加载数据
        if (!this.isPot) {
          this.status = 'loading';
          
          //loadmore加载下一页数据
          this.page = this.page + 1;
          
          if (this.value != '') {
            var data = {
              "page": this.page,
              "rows": 8,
              "pastName": this.value
            }
          } else {
            var data = {
              "page": this.page,
              "rows": 8
            }
          }
          
          
          uni.request({
            url: 'http://ocr.server.truedei.com/ocr/pest/getPestList',
            method: 'POST',
            data: data,
            success: res => {
              console.log(res);
              var data = res.data.result.list;
              this.petInfos = this.petInfos.concat(data);
              
              //判断是否为最后一页
              if (res.data.result.isLastPage) {
                this.isPot = true;
                this.status = 'nomore';
              } else {
                this.status = 'loadmore';
              }
            },
            fail: err => {
              reject(err);
            }
          });
        }
      },
      //关键字查询害虫
      search(value){
        
        console.log("搜索触发");
        var value = this.value;
        this.page = 1;
        this.isPot = false;
        this.status = 'loadmore';
        
        //获取搜索的关键字
        if (value === '') {
          this.loadmore();
          return;
        }
        
        uni.request({
          url: 'http://ocr.server.truedei.com/ocr/pest/getPestList',
          method: 'POST',
          data: {
            "page": this.page,
            "rows": 8,
            "pastName": this.value
          },
          success: res => {
            console.log(res);
            
            this.petInfos = res.data.result.list;
            
            //判断是否为最后一页
            if (res.data.result.isLastPage) {
              this.isPot = true;
              this.status = 'nomore';
            } else {
              this.status = 'loadmore';
            }
          },
          fail: err => {
            reject(err);
          }
        });
      },
      change(value) {
      	// 搜索框内容变化时，会触发此事件，value值为输入框的内容
      	//console.log(value);
      }
    }
  }
</script>

<style lang="scss" scoped>
  .content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0;
  }

  .logo {
    height: 200rpx;
    width: 200rpx;
    margin-top: 100rpx;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 50rpx;
  }

  .search {
    margin: 40rpx auto 0;
    height: 60rpx;
    width: 100%;
    // background-color: red;
  }

  .text-area {
    display: flex;
    justify-content: center;
  }

  .title {
    font-size: 28rpx;
    color: $u-content-color;
  }

  .link-demo {
    margin-top: 80rpx;
  }

  .petInfo {
    color: #2979ff;
    text-decoration: underline;
  }

  .pets-box {
    margin: 0 auto;
    width: 100%;
  }

  .pets-box .pet-box {
    padding-left: 1em;
    margin: 40rpx auto 0;
    width: 100%;
    height: 320rpx;
    border: 1rpx solid #bebebe;
    border-radius: 14rpx;
    box-shadow: 0rpx 0rpx 12rpx #82848A;
  }

  .u-search-inner {
    background-color: rgb(234, 234, 234);
    margin-top: 30rpx;
    border-radius: 100rpx;
    display: flex;
    align-items: center;
    padding: 10rpx 16rpx;
  }

  .load-more {
    margin-top: 30rpx;
  }

  .pet-image,
  .pet-info {
    margin: 20rpx auto;
    width: 280rpx;
    height: 280rpx;
  }

  .pet-info .pet-name {
    font-size: 36rpx;
    font-weight: 700;
  }

  .pet-info .pet-plots {
    color: grey;
    font-weight: 700;
  }

  .pet-position {
    font-weight: 700;
  }

  .pet-position-v {
    white-space: nowrap;
    /* 2.超出部分隐藏 */
    overflow: hidden;
    /* 3.文字用省略号替代超出的部分 */
    text-overflow: ellipsis;
  }
</style>
